import { Component, NgZone, OnInit, ViewChild } from '@angular/core';
import { Router } from "@angular/router";
import { ApiservicesService } from '../../provider/apiservices.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { DataTransferService } from '../../services/data-transfer.service';
import { EventsService } from 'src/app/services/events.service';
import { NavController } from '@ionic/angular';
import { IonBackButtonDelegate } from '@ionic/angular';
import { Location } from '@angular/common';

@Component({
  selector: 'app-say-cheers-message',
  templateUrl: './say-cheers-message.page.html',
  styleUrls: ['./say-cheers-message.page.scss'],
})

export class SayCheersMessagePage implements OnInit{

  @ViewChild(IonBackButtonDelegate, { static: false }) backButton: IonBackButtonDelegate;
  cardListData:any;
  textAreaText:any="";
  cardListSettingData:any;
  pageHeader:any;
  
  constructor(private router:Router,private apiservices:ApiservicesService,private zone:NgZone,private sendData:DataTransferService,
    private eventService:EventsService,private navCtrl:NavController,private location:Location) {

    this.cardListData=this.sendData.alldata;
    console.log("card List Data==", this.cardListData);
    this.cardListSettingData=this.sendData.myParam;
    console.log("card List cardListSettingData==", this.cardListSettingData);
    this.pageHeader=this.sendData.myParam.page_header;

  }









  ionViewDidEnter(){
    console.log('ionViewDidEnter');
    this.setUIBackButtonAction();
  }

  setUIBackButtonAction(){
    this.backButton.onClick = () => {
      console.log('clicked');
      if(this.apiservices.previewData==false){
        // this.navCtrl.back();
        this.location.back();
      }else{
        this.apiservices.previewData=false;
        this.pageHeader=this.sendData.myParam.page_header;
      }
    };
  }

  ngOnDestroy(): void{
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.apiservices.previewData=false;
    this.pageHeader=this.sendData.myParam.page_header;
  }

  ngOnInit(){}

  previewPage(){
    this.apiservices.previewData=true;
    this.pageHeader=this.sendData.myParam.preview_page_header;
    // this.pageHeader="Preview Page";
  }

  goToNext(){
    this.zone.run(()=>{
      console.log("brand pending api function==");
      let apiKey={
        "receiver": this.cardListData.sayCheersListData.employee_id,
        "category_id": this.cardListData.ArrayData.category_id,
        "card_id": this.cardListData.listData.card_id,
        "citation": this.textAreaText,
        "badge_image":this.cardListData.listData.card_image
      };
      //following key will be used in apiservice for keeping original comment obj
      let copy_apiKey={
        "receiver": this.cardListData.sayCheersListData.employee_id,
        "category_id": this.cardListData.ArrayData.category_id,
        "card_id": this.cardListData.listData.card_id,
        "citation": this.textAreaText,
        "badge_image":this.cardListData.listData.card_image
      };
      console.log("brand pending api keys==", apiKey);
      // this.apiservices.apiCallWithLoginToken(URLS.submitSayCheers, apiKey).subscribe((result) => {
      this.apiservices.apiCallWithLoginTokenComment(URLS.submitSayCheers, apiKey, copy_apiKey).subscribe((result)=>{
        console.log("brand pending Api Result==", result);
        if (result.status == 1) {
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
          console.log("brand detail result==", result);
          var data={
            "commingFrom":"reloadHomeApi",
            "reloadHomePage":true
          }
          this.eventService.publishSomeData({data});
          this.router.navigate(["/say-cheers-board"])
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      }, err=>{
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    });
  }








  
}